.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

*{
  padding: 0;
  margin: 0;
  list-style: none;
  text-decoration: none;
  color: white;
}

div{
  background-color: rgba(0, 0, 0, 0.6);
  border: 1px solid rgb(0, 39, 39);
  border-radius: 10px;
  padding:10px 25px 10px 25px;
}

div:nth-child(2n +2 ){
  background-color: rgb(0, 25, 23);
}

li{
  background-color: rgba(0, 0, 0, 0.6);
  border: 1px solid rgb(0, 39, 39);
  border-radius: 10px;
  padding:10px 25px 10px 25px;
  margin-bottom: 10px;
  margin-top:10px;
}

li:nth-child(2n +2 ){
  background-color: rgb(0, 25, 23);
}

li:hover{
  background-color: rgb(0, 90, 83);
}

h1,h2,h3,h4,h5{
  margin-bottom: 10px;
  text-shadow: 1px 1px rgb(147, 147, 147);
}
p{
  margin-bottom: 10px;
}

.formInscription{
  justify-content: center;
}